import uniRate from "@/components/uni-rate/uni-rate.vue"

export default {
	data() {
		return {
			listinfo: {},
			rate: 3.3,
			logined:false,
			userinfo:{}
		}
	},
	
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		if(userinfo != null){
			me.userinfo = userinfo;
			me.logined = userinfo.logined;
			
			//this.rate = this.listinfo.score/this.listinfo.scoreNumber;
			
		}else{
			me.logined = false;
			me.userinfo = {};
		}
	},
	
	onLoad(params) {
		console.log(params);
		this.rate =params.rate;
		var me = this;
		var id = params.id;
		// console.log("id:"+id);
		
		uni.request({
			url:this.mainUrl+'order/order/findOrderInfoById',
			method:"GET",
			data:{
				order_id:id
			},
			success:(res) => {
				console.log("---"+this.rate);
				var listinfo = res.data.data;
				this.listinfo = listinfo;
				// this.rate = listinfo.score/listinfo.scoreNumber;
				// console.log(this.rate);
				// this.app.$forceUpdate();
				// uni.request({
				// 	url:this.mainUrl+"user/evalute/getByOrderId",
				// 	data:{
				// 		order_id:id
				// 	}
				// })
			}
		})
	},
	
	methods: {
		join:function(){
			uni.request({
				url:this.mainUrl+"user/message/apply",
				data:{
					user_id:this.userinfo.userId,
					order_id:this.listinfo.orderId,
					desc:""
				},
				method:"GET",
				success: (res) => {
					console.log("res:");
					console.log(res);
					if(res.data.status=="success"){
						uni.showToast({
							title:"请求已发送"
						})
					}else{
						uni.showToast({
							mask:true,
							title:res.data.data.errMsg,
							image:"../../static/error.png"
						})
					}
				}
			})
		}
	},
	
	components: {
		uniRate
	}
}
